<template>
  <q-layout view="hhh Lpr lFf">
     <q-toolbar v-if="$route.meta.showBackArrow">
          <q-btn flat round dense icon="arrow_back" @click="goBack" />
          <q-toolbar-title class="text-center">
            {{ $route.meta.title }}
          </q-toolbar-title>
      </q-toolbar>

    <q-header v-if="$route.meta.showBackArrow === undefined" class="bg-white q-ml-md q-mt-md">
        <q-btn color='primary' class='q-mr-sm shadow-10' no-caps label="Gitarre | Bass" @click="navigateTo('/desktop/instruments/guitar')"></q-btn>
        <q-btn color='primary' class='q-mr-sm shadow-10' no-caps label="Schlagzeug | Piano" @click="navigateTo('/desktop/instruments/piano-and-drums')"></q-btn>
        <q-btn color='primary' class='q-mr-sm shadow-10' no-caps label="Proberaum" @click="navigateTo('/desktop')"></q-btn>
        <q-btn color='primary' class='q-mr-sm shadow-10' no-caps label="Einstellungen" @click="navigateTo('/desktop/room')"></q-btn>
    </q-header>


    <q-page-container>
      <router-view />
    </q-page-container>
  </q-layout>
</template>

<script setup>
import { useRouter } from 'vue-router'
import { useInstrumentStore } from '@/stores/instruments-store';
import { useQuasar } from 'quasar'

// Switch to light mode for wall display
const $q = useQuasar();
$q.dark.set(false);

const store = useInstrumentStore();
store.checkBackendAvailability();

const router = useRouter();

function navigateTo(route) {
  router.push(route)
}

function goBack() {
  router.go(-1);
}


</script>